import {CodeServicesProvider} from '../../providers/code-services/code-services';
import {GlobalComponent} from '../../components/global/global';
import {LoadingController} from 'ionic-angular';
import { Component } from "@angular/core";
import {
  IonicPage,
  NavController,
  NavParams,
  ViewController
} from "ionic-angular";
import { CodeViewModel } from "../../Model/ViewModel/CodeViewModel";

/**
 * Generated class for the CodeModalPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: "page-main-code-copy-modal",
  templateUrl: "main-code-copy-modal.html"
})
export class MainCodeCopyModalPage {
  item: any;
  from_item:any;
  mode: string;
  CanEditCode: boolean;
  title:string;
  code_type:string;
  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public viewCtrl: ViewController,
    private CodeServices:CodeServicesProvider,
    public loadingCtrl: LoadingController,
    private global: GlobalComponent,
  ) {
    this.code_type="000";
    this.item = new CodeViewModel();
    this.item.code_key = navParams.data.item.code_key;
    this.item.code_type = navParams.data.item.code_type;
    this.item.code_no = navParams.data.item.code_no;
    this.item.code_desc = navParams.data.item.code_desc;
    this.item.creator = navParams.data.item.creator;
    this.item.create_time = navParams.data.item.create_time;
    this.item.modifier = navParams.data.item.modifier;
    this.item.last_update_time = navParams.data.item.last_update_time;
    this.item.display_order_no=navParams.data.item.display_order_no;
    this.item.code_value=navParams.data.item.code_value;
    this.from_item=navParams.data.from_item;

    this.CanEditCode = navParams.data.CanEditCode;
    this.mode = navParams.data.mode;
    this.title="主代碼複製";
  }
  get_max_code_no() {
    let code_key=this.code_type+"|"+this.item.code_no;
    this.global.createLoader("取得最大值中...");
    this.global.loading.present().then(() => {
      this.CodeServices.GetMaxCodeNoAsync
        (code_key)
        .subscribe(
          data => {

            if (data.DidError === true)
            {
              this.global.dismissLoading();
             this.global.showError(data.ErrorMessage);
            }
            else {
             this.item.code_no=data.Model;
              this.global.dismissLoading();
            }
          },
          err => {
            this.global.dismissLoading();
            this.global.showError("無法連上WebAPI伺服器-"+err.message);
          }
        );

    });
  }
  get_max_display_order_no() {
    let code_key=this.code_type+"|";
    this.global.createLoader("取得最大值中...");
    this.global.loading.present().then(() => {
      this.CodeServices.GetMaxDisplayOrderNoAsync
        (code_key)
        .subscribe(
          data => {

            if (data.DidError === true)
            {
              this.global.dismissLoading();
             this.global.showError(data.ErrorMessage);
            }
            else {
             this.item.display_order_no=data.Model;
              this.global.dismissLoading();
            }
          },
          err => {
            this.global.dismissLoading();
            this.global.showError("無法連上WebAPI伺服器-"+err.message);
          }
        );

    });
  }
  Save() {
    this.viewCtrl.dismiss(this.item);
  }
  close() {
    this.viewCtrl.dismiss();
  }
  ionViewDidLoad() {
    console.log("ionViewDidLoad CodeModalPage");
  }
}



