import {StringResponse} from '../../Model/Response/StringResponse';
import { String } from "../../Model/String";
import { Injectable } from "@angular/core";
import { ServicesProvider } from "../services/services";
import { CodeListResponse } from "../../Model/Response/CodeListResponse";
import { CodeResponse } from "../../Model/Response/CodeResponse";
import { CodeViewModel } from '../../Model/ViewModel/CodeViewModel';

/*
  Generated class for the CodeServiceProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/

@Injectable()
export class CodeServicesProvider {
  private Codes: CodeListResponse;
  private Code: CodeResponse;
   ctl: string;

  constructor(
  public Services: ServicesProvider
  ) {
    this.ctl = "Code";
    console.log("Hello CodeServiceProvider Provider");
  }

  public GetListsByAsync(PageSize: number, PageNumber: number, keyword: string,code_type:string,code_key:string,main_code_type:string,order_type:boolean) {
    var sub_url: string;
     sub_url = "Code?PageSize={1}&PageNumber={2}&keyword={3}&code_type={4}&code_key={5}&main_code_type={6}&order_type={7}";
    sub_url = String.Format(sub_url, this.ctl, PageSize, PageNumber, keyword,code_type,code_key,main_code_type,order_type);
    return this.Services.GetAsync(sub_url,this.ctl,true).map(item => {
      this.Codes = new CodeListResponse({
        Message: item.Message,
        DidError: item.DidError,
        ErrorMessage: item.ErrorMessage,
        PageSize: item.PageSize,
        PageNumber: item.PageNumber,
        TotalRows: item.TotalRows,
        PageRows: item.PageRows,
        TotalPages: item.TotalPages,
        Model: item.Model
      });
      return this.Codes;
    });
  }



  public PostPutAsync(CodeViewModel:CodeViewModel,mode:string) {
    var sub_url: string;
    if(mode==="POST")
      sub_url = "Code";
    else
    sub_url = "Code/{1}";
    sub_url = String.Format(sub_url, this.ctl,CodeViewModel.code_key);
    return this.Services.PostPutAsync(sub_url, this.ctl, true,CodeViewModel,mode).map(
      item => {
        this.Code = new CodeResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });

        return this.Code;
      }
    );
  }

  public DeleteAsync(code_key:string) {
    var sub_url: string;
    sub_url = "Code/{1}";
    sub_url = String.Format(sub_url, this.ctl,code_key);

    return this.Services.DeleteAsync(sub_url, this.ctl, true).map(
      item => {
        this.Code = new CodeResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });
        return this.Code;
      }
    );
  }
  public GetMaxCodeNoAsync(code_key:string) {
    var sub_url: string;
    sub_url = "Code/get_max_code_no/{1}";
    sub_url = String.Format(sub_url, this.ctl,code_key);

    return this.Services.GetAsync(sub_url, this.ctl, true).map(
      item => {
        return  new StringResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });
      }
    );
  }

  public GetMaxDisplayOrderNoAsync(code_key:string) {
    var sub_url: string;
    sub_url = "Code/get_max_display_order_no/{1}";
    sub_url = String.Format(sub_url, this.ctl,code_key);

    return this.Services.GetAsync(sub_url, this.ctl, true).map(
      item => {
        return  new StringResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });
      }
    );
  }
  public GetSqlAsync(code_key:string,method:string) {
    var sub_url: string;
    sub_url = "Code/get_{2}_sql/{1}";
    sub_url = String.Format(sub_url, this.ctl,code_key,method);

    return this.Services.GetAsync(sub_url, this.ctl, true).map(
      item => {
        return  new StringResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });
      }
    );
  }
}



