import { SingleModelResponse } from "./SingleModelResponse";
import { CodeViewModel } from "../ViewModel/CodeViewModel";

export class CodeResponse extends SingleModelResponse<CodeViewModel> {
  Model: CodeViewModel;
  constructor(obj?: any) {
    super(obj);
    this.Message = obj.Message;
    this.DidError = obj.DidError;
    this.ErrorMessage = obj.ErrorMessage;
    if (this.DidError === false) {
      this.Model = new CodeViewModel();
      this.Model.code_key=obj.Model.code_key;
      this.Model.code_type=obj.Model.code_type;
      this.Model.code_no=obj.Model.code_no;
      this.Model.code_desc=obj.Model.code_desc;
      this.Model.creator=obj.Model.creator;
      this.Model.create_time=obj.Model.create_time;
      this.Model.modifier=obj.Model.modifier;
      this.Model.last_update_time=obj.Model.last_update_time;
      this.Model.display_order_no = obj.Model.display_order_no;
      this.Model.code_value = (obj && obj.Model.code_value) || null;

    }
  }
}



