export class CodeViewModel {
  /// <summary>
  ///  代碼主鍵
  /// <summary>
  public code_key: string;
  /// <summary>
  ///  代碼型態
  /// <summary>
  public code_type: string;
  /// <summary>
  ///  代碼
  /// <summary>
  public code_no: string;
  /// <summary>
  ///  代碼說明
  /// <summary>
  public code_desc: string;
  /// <summary>
  ///  建立者
  /// <summary>
  public creator: string;
  /// <summary>
  ///  建立時間
  /// <summary>
  public create_time: Date;
  /// <summary>
  ///  最後更新者
  /// <summary>
  public modifier: string;
  /// <summary>
  ///  最後更新時間
  /// <summary>
  public last_update_time: Date;
  /// <summary>
  ///  顯示順序編號
  /// <summary>
  public display_order_no: number;
  /// <summary>
  ///  代碼值
  /// <summary>
  public code_value: string;
}



